import mvListT from '@/components/mvListT.vue';
export default {
	data() {
		return {
			tabNum: 1,
			mvFirstList: '',
			topMvList: '',
			personalizedMvList: ''
		};
	},
	onLoad() {
		this.getMvFirstList(); //获取最新MV
		this.getTopMvList(); //获取mv排行
		this.getPersonalizedMvList(); //获取推荐mv
	},
	components: {
		nowPlay,
		mvListT
	},
	methods: {
		getMvFirstList() {
			//获取最新MV
			var _this = this;
			uni.showLoading({
				title: '加载中'
			});
			uni.request({
				url: _this.MusicApi.server + _this.MusicApi.type.mvFirst,
				success: res => {
					uni.hideLoading();
					//console.log(res.data.data);
					if (res.data.code == 200) {
						_this.mvFirstList = res.data.data;
					} else {
						//console.error("数据获取失败");
						uni.showToast({
							title: 'banner数据获取失败',
							duration: 5000,
							icon: 'none'
						});
					}
				},
				fail: err => {
					uni.hideLoading();
					uni.showToast({
						title: '请求超时',
						duration: 5000,
						icon: 'none'
					});
				}
			});
		},
		getTopMvList() {
			//获取MV排行
			var _this = this;
			uni.showLoading({
				title: '加载中'
			});
			uni.request({
				url: _this.MusicApi.server + _this.MusicApi.type.topMv,
				success: res => {
					uni.hideLoading();
					//console.log(res.data.data);
					if (res.data.code == 200) {
						_this.topMvList = res.data.data;
					} else {
						//console.error("数据获取失败");
						uni.showToast({
							title: 'banner数据获取失败',
							duration: 5000,
							icon: 'none'
						});
					}
				},
				fail: err => {
					uni.hideLoading();
					uni.showToast({
						title: '请求超时',
						duration: 5000,
						icon: 'none'
					});
				}
			});
		},
		getPersonalizedMvList() {
			//获取推荐MV
			var _this = this;
			uni.showLoading({
				title: '加载中'
			});
			uni.request({
				url: _this.MusicApi.server + _this.MusicApi.type.personalizedMv,
				success: res => {
					uni.hideLoading();
					if (res.data.code == 200) {
						_this.personalizedMvList = res.data.result;
						//console.log(_this.personalizedMvList)
					} else {
						//console.error("数据获取失败");
						uni.showToast({
							title: 'banner数据获取失败',
							duration: 5000,
							icon: 'none'
						});
					}
				},
				fail: err => {
					uni.hideLoading();
					uni.showToast({
						title: '请求超时',
						duration: 5000,
						icon: 'none'
					});
				}
			});
		}
	}
};
